/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as React from "react";
import Img from "gatsby-image";
import {Link} from "gatsby";

import * as styles from "./TimelinePost.styles"

interface TimelinePostProps {
    node: {
        frontmatter: {
            slug: string
            headerImage: {
                childImageSharp: {
                    fluid: {
                        base64: string;
                        aspectRatio: number;
                        src: string;
                        srcSet: string;
                        sizes: string;
                    }
                }
            }
            pageTitle: string
            pageDescription: string
            headerImageAlt: string
            initially_published: string
            last_modified: string
            tags: string[]
        }
    }
}

const TimelinePost = (props: TimelinePostProps) => {
    const {node} = props;
    return <div style={{border: "", margin: "0 0 16px 0"}} css={styles.flex}>
        <div style={{padding: "8px"}}>
            <Img
                fluid={node.frontmatter.headerImage.childImageSharp.fluid}
                alt={node.frontmatter.headerImageAlt}
                style={{height: "100px", width: "100px"}}
                imgStyle={{objectFit: "contain"}}
            />
        </div>
        <div style={{padding: "8px"}}>
            <div>
                <Link to={node.frontmatter.slug} key={node.frontmatter.slug}>
                    <b>
                        {node.frontmatter.pageTitle}
                    </b>
                </Link>
            </div>
            <div>
                {node.frontmatter.pageDescription}
            </div>
            <small>
                {node.frontmatter.initially_published}
                {". "}
                Tags:
                {" "}
                {node.frontmatter.tags.map(tag => {
                    return <Link to={`/tags/${tag}`} css={styles.tagLink} key={`${node.frontmatter.slug}/tag/${tag}`}>
                        {tag}
                    </Link>
                })}
            </small>
            <div>
            </div>
        </div>
    </div>
}

export default TimelinePost;
