/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as React from "react"
import {Helmet} from "react-helmet"
import {graphql} from "gatsby"

import PageLayout from "../components/PageLayout";
import TimelinePost from "../components/LandingPageTimeline/TimelinePost";
import * as style from "../styles/LandingPage.styles";

interface IndexPageProps {
    data: {
        allMarkdownRemark: {
            edges: {
                node: {
                    frontmatter: {
                        slug: string;
                        headerImage: {
                            childImageSharp: {
                                fluid: {
                                    base64: string;
                                    aspectRatio: number;
                                    src: string;
                                    srcSet: string;
                                    sizes: string;
                                }
                            }
                        };
                        pageTitle: string;
                        pageDescription: string;
                        headerImageAlt: string
                        initially_published: string
                        last_modified: string
                        tags: string[]
                    }
                }
            }[]
        }
    }
}

const IndexPage = (props: IndexPageProps) => {
    const {data} = props;

    const header = <div css={style.headerSection}>
        <h1>Arshi Annafi</h1>
        <h2>Software Engineer</h2>
    </div>;

    const content = <div>
        {data.allMarkdownRemark.edges.map(edge => {
            return <TimelinePost key={edge.node.frontmatter.slug} node={edge.node}/>
        })}
    </div>

    return (
        <main>
            <Helmet>
                <meta charSet="utf-8"/>
                <meta name="viewport" content="width=device-width, initial-scale=1"/>
                <title>Arshi Annafi</title>
                <meta name="description"
                      content="Arshi Annafi | Software Engineer | A place where I share my journey in software development and technology, showcase projects, and post articles and resources."/>
            </Helmet>
            <PageLayout
                header={header}
                content={content}
            />
        </main>
    )
}

export default IndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: {fields: frontmatter___initially_published, order: DESC}) {
      edges {
        node {
          frontmatter {
            slug
            headerImage {
              childImageSharp {
                fluid(maxHeight: 100) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            headerImageAlt
            pageTitle
            pageDescription
            initially_published
            last_modified(formatString: "YYYY-MM-DD")
            tags
          }
        }
      }
    }
  }
`;
